Form Treatment
-------------------------------------------------------------------------------- */



input {
  margin: 5px;
  margin-bottom: 10px;
}

/* --------------------------------------------------------------------------------
New Message
-------------------------------------------------------------------------------- */
.ui.grid {
  display: flex !important;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  left: 3.5rem;
  position: absolute;
  top: 7rem;
  left: 12rem;
  /* background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,1) 90%);  */
}
.ui-container{
  display: flex;
  position: relative;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}

.ui.fluid.column {
  display: flex !important;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}
.title.icon.container {
  padding: 20px;
  height: 100% !important;
  width: 100%;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-around;
  display: flex;

}
.ui.center.aligned.header {
  font-family: 'Raleway', sans-serif !important;
  display: flex !important;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin-right: 5.35rem;
  padding: 15px;
  border-radius: 5px;
  color: white;
  text-align: left;
  background: rgb(145, 26, 108);
  background: linear-gradient(
    90deg,
    rgba(145, 26, 108, 1) 0%,
    rgba(211, 20, 89, 1) 100%
  );
}

.checklist {
  width: 140px !important;
  height: auto;
  padding-right: 1rem;
  margin-top: -10px;
  margin-bottom: -15px;
  margin-left: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  align-self: start; 
  flex-wrap: wrap;
}

.ui.card {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  align-self: start; 
  flex-wrap: wrap;
  height: 75px;
  padding: 20px;
  border-radius: 1px;
  width: auto;
  border: none !important;
  background-color: #191c25;
  box-shadow: none;
}

.step-text {
  vertical-align: top;
  display: flex;
  flex-wrap: wrap;
  font-weight: bold;
  font-size: x-large;
  color: rgb(145, 26, 108);
  color: linear-gradient(
    90deg,
    rgba(145, 26, 108, 1) 0%,
    rgba(211, 20, 89, 1) 100%
  );
}

.ui.selection.dropdown {

  padding: 2px;
  width: 200px;

}

.card-content {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  border-radius: 5px;
  height: 150px;
  padding: 10px;
  background-color: #191c25;
}

.card-cont:hover{
  background-color: #cacbcd;
  background-image: none;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgb(34 36 38 / 15%) inset;
  color: rgba(0,0,0,.8);
}

.column {
  display: flex !important;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}
.three.row.column {
  display: flex;
  position: relative;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin-right: 6.25rem;
}

.one-column-row {
  display: flex !important;
  position: relative;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin-right: 5.5rem;
}

.select-container {
  position: absolute;
  left: 250px;
  top: 350px;
}

.ui.button{
  margin-right: 6.25rem;
  padding: 15px;
  color: white;
  font-family: 'Raleway', sans-serif !important;
  background-color: rgba(175,23,98,255) !important;
  background-color: linear-gradient(
    90deg,
    rgba(145, 26, 108, 1) 0%,
    rgba(211, 20, 89, 1) 100% 
  ) !important;
}

button.login.ui.button:hover{
  background-color: red !important;
}


.vl {

  border-left: 1.5px solid #191c25;
  height: 100px;
  position: absolute;
  left: 34.25%;
  top: 2.5rem;
}
.vl2 {

  border-left: 1.5px solid #191c25;
  height: 100px;
  position: absolute;
  left: 65.5%;
  top: 2.5rem;
}

.message-display{
  font-family: 'Inconsolata', monospace !important;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-right: 4.75rem;
  display: flex !important;
  position: relative;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  color: #33ff33;
  font-size: 17.5px;
}

